import React from 'react';
import './Project.css';
import { Element } from 'react-scroll';
import Carousel from 'react-bootstrap/Carousel';
import login from './img/login.png';
import register from './img/register.png';
import home from './img/home.png';
import compliantReg from './img/compliantReg.png';
import editupdate from './img/editupdate.png';
import compdel from './img/compdel.png';
import icondropdown from './img/icondropdown.png';
import mobileHome from './img/mobileHome.png';
import sidenav from './img/sidenav.png';
import mobilecompliant from './img/mobilecompliant.png';

const Project = () => {
  return (
    <Element className='project' name='projects'>
      <div className='container'>
      <u style={{color:"#f64c08"}}><h4 className='heading text-white'>Project</h4></u>
        

        <div>
          <details>
            <summary>Project Tittle Click Here </summary><br />
            <cite className='tit'>Compliant Management System using MERN - Stack. it's my own project. </cite>
          </details>
        </div><br />
        
        <div className='slider'>
        <Carousel>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src={login}
              alt="First slide"
            />
            <Carousel.Caption className='mb-1'>
            <p className='caption'>Login page<br/>
              In this page used to client-side validation.</p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src={register}
              alt="Second slide"
            />

            <Carousel.Caption>
            <p className='caption'>Register page<br/>
              In this page also used to client-side validation.</p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src={home}
              alt="Third slide"
            />

            <Carousel.Caption>
            <p className='caption'>Home page<br/>
              View all  the registered compliant.</p>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img
              className="d-block w-100"
              src={compliantReg}
              alt="Third slide"
            />

            <Carousel.Caption>
            <p className='caption'>Compliant Register page<br/>
             Student register the type of compliant.</p>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img
              className="d-block w-100"
              src={editupdate}
              alt="Third slide"
            />
            <Carousel.Caption>
            <p className='caption'>Compliant edit page<br/>
             Get the compliant and update it .</p>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img
              className="d-block w-100"
              src={compdel}
              alt="Third slide"
            />
            <Carousel.Caption>
            <p className='caption'>Delete Compliant<br/>
             if you want to delete the compliant delete button click to delete.</p>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img
              className="d-block w-100"
              src={icondropdown}
              alt="Third slide"
            />

            <Carousel.Caption>
            <p className='caption'>Icondropdown</p>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img
              className="d-block w-100"
              src={mobileHome}
              alt="Third slide"
            />
            <Carousel.Caption>
            <p className='caption'>Responsive Home</p>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img
              className="d-block w-100"
              src={sidenav}
              alt="Third slide"
            />
            <Carousel.Caption>
            <p className='caption'>REsponsive Sidebar</p>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img
              className="d-block w-100"
              src={mobilecompliant}
              alt="Third slide"
            />
            <Carousel.Caption>
            <p className='caption'>Responsive Compliant page</p>
            </Carousel.Caption>
          </Carousel.Item>


        </Carousel>
        </div>
      </div>
    </Element>
  )
}

export default Project;