import React from 'react';
import './Topcontainer.css';
import { Element } from 'react-scroll';
import Topcontent from './Topcontect/Topcontent';

const Topcontainer = () => {
  return (
    <div>
        <Element name="about" className="topcontainer">
           <Topcontent/>
        </Element>
    </div>
  )
}

export default Topcontainer