import React,{useState} from 'react';
import './Contact.css';
import { Element } from 'react-scroll';
import { toast } from 'react-toastify';
import Icon from 'react-icons-kit';
import {send} from 'react-icons-kit/fa/send';

const Contact = () => {

    
  const [detail, setDetail] = useState({
    sname: "",
    email: "",
    Mobile:""
  });

  const addCompliant = () => {
      toast.success(" Happy Connected !!!");
    }

    const handleChange = (data) => {
        setDetail({ ...detail, [data.target.name]: data.target.value });
      }
    

  return (
    <Element className='contac' name='contact'>
        <div className="container">
        <u style={{color:"#f64c08"}}><h4 className='heading mb-3 text-white'>Contact</h4></u>
            <div  className="jumbotron forms">
              <h3 className="head">Join With Me</h3>
              <form className="was-validated row" onSubmit={addCompliant} autoComplete="off"><hr />

                <div className="form-group f1 col-md-12 col-sm-12 " >
                  <label htmlFor="name" >Name:</label>
                  <input type="text"
                    className="form-control text" id="name" name="sname"
                    placeholder="Type your name" required
                    onChange={handleChange}
                    value={detail.sname}
                  />
                  <div className="invalid-feedback">Please enter your name.</div>
                </div>

                <div className="form-group f2 col-md-12 col-sm-12 ">
                  <label htmlFor="email" >Email:</label>
                  <input type="text" className="form-control text"
                    id="email" name="email"
                    value={detail.email}
                    onChange={handleChange}
                    placeholder="Type your email" required />
                  <div className="invalid-feedback">Please enter your email.</div>
                </div>

                <div className="form-group f3 col-md-12 col-sm-12 " >
                  <label htmlFor="regnumber">Mobile:</label>
                 <input type="text" className="form-control text"
                    id="regnumber" name="registernumber"
                    value={detail.registernumber}
                    onChange={handleChange}
                    placeholder="Type your number" required /> 
                  <div className="invalid-feedback">Please enter your Number.</div>
                </div>

                
                <div className="form-group ">
                  <button className="btn btn-warning  mt-3" type="submit" aria-required>Join Now  <Icon size={15} icon={send} /></button>
                </div>


              </form>
            </div>
        </div>
    </Element>
  )
}

export default Contact