import React from 'react';
import './Skills.css';
import Skil from './Skil.jpg';
import ProgressBar from 'react-bootstrap/ProgressBar';
import { Element } from 'react-scroll';

const Skills = () => {
   const html =85;
   const css =80;
   const js =75;
   const rj = 70;
   const nj = 60;
   const cj=80;

  return (
    <Element className='skills' name='Skills'>
        <div className='container'>
        <div className='row'>
            <div className='col-xl-6 col-lg-12 col-md-12 col-sm-12 br-1 img'>
                <img height={400} width={550} alt="png.img" color="black" src={Skil} />
            </div>
            <div className='col-xl-6 col-lg-12 col-md-12 col-sm-12 skl'>
                <h4 className='title'>Skills</h4>
                <div className='row list'>
                    <div className='col-12 skls'>
                      <h4 className='list'>HTML</h4>
                      <ProgressBar now={html} label={`${html}%`} animated/>
                    </div>
                    <div className='col-12 skls'>
                      <h4 className='list'>CSS</h4>
                      <ProgressBar now={css} label={`${css}%`} variant="info" animated/>
                    </div>
                    <div className='col-12 skls'>
                      <h4 className='list'>JavaScript</h4>
                      <ProgressBar now={js} label={`${js}%`} variant="success" animated/>
                    </div>
                    <div className='col-12 skls'>
                      <h4 className='list'>React JS</h4>
                      <ProgressBar now={rj} label={`${rj}%`} variant="warning" animated/>
                    </div>
                    <div className='col-12 skls'>
                      <h4 className='list'>Node JS</h4>
                      <ProgressBar now={nj} label={`${nj}%`} variant="danger" animated/>
                    </div>
                    <div className='col-12 skls'>
                      <h4 className='list'>Core java</h4>
                      <ProgressBar now={cj} label={`${cj}%`} variant="dark" animated/>
                    </div>

                </div>
            </div>
        </div>
        </div>
    </Element>
  )
}

export default Skills;